import React, { useContext, useState } from 'react';
import { Form, Avatar, Button, Input, Row, Col, Card, message, Upload } from 'antd';
import { UserOutlined, UploadOutlined } from '@ant-design/icons';
import { ROW_GUTTER } from 'constants/ThemeConstant';
import { UserContext } from 'hooks/UserContextProvider';
import { BASE_SERVER_URL } from 'configs/AppConfig';
import IntlMessage from 'components/util-components/IntlMessage';
import { useMutation, useQuery } from '@apollo/client';
import { ME, MY_PROFILE, UPDATE_PROFILE } from 'graphql/user';

const { TextArea } = Input;

const UpdateProfile = () => {

    const phoneRegEx = new RegExp(/^[7-9][0-9]{3}[0-9]{4}$/u);
    const [image, setImage] = useState("")

    const [form] = Form.useForm();

    const contextData = useContext(UserContext)
    const [user] = useState(contextData.user);

    const { refetch } = useQuery(MY_PROFILE, {
        onCompleted: data => {
            form.setFieldsValue(data.accountSelf)
        }
    })

    const imageProps = {
        accept: '.jpg',
        name: 'file',
        multiple: false,
        listType: "picture-card",
        showUploadList: false,
        beforeUpload: file => {
            return false
        }
    };

    const [update, { loading: updateLoading }] = useMutation(UPDATE_PROFILE, {
        refetchQueries: [{ query: ME }],
        onCompleted: data => {
            message.success("Амжилттай шинэчиллээ!");
            refetch();
        }
    })

    const onFinish = (values) => {
        values.image = image
        update({ variables: values })
    }

    function onUploadAvatar(info) {
        setImage(info.file);
    }

    return (
        <Card>
            <Row>
                <Col xs={24} sm={24} md={8} className='text-center mt-4'>
                    <div className="mt-5">
                        <Upload onChange={onUploadAvatar} {...imageProps} >
                            {image ?
                                <img src={URL.createObjectURL(image)} alt="avatar" className="img-fluid max-h-40" />
                                :
                                <div>
                                    {user.isStudent ?
                                        <img src={BASE_SERVER_URL + user.student.photo} alt="avatar" className="img-fluid max-h-40" /> : user.isTeacher ?
                                        <img src={BASE_SERVER_URL + user.teacher.photo} alt="avatar" className="img-fluid max-h-40" /> :
                                        <img src={BASE_SERVER_URL + 'default.jpg'} alt="avatar" className="img-fluid max-h-40" /> 
                                    }
                                </div>
                            }
                        </Upload>
                    </div>
                </Col>
                <Col xs={24} sm={24} md={16}>
                    <div className="mt-4">
                        <Form
                            name="basicInformation"
                            layout="vertical"
                            onFinish={onFinish}
                            form={form}
                        >
                            <Row>
                                <Col xs={24} sm={24} md={24} lg={24}>
                                    <Row gutter={ROW_GUTTER}>
                                        <Col xs={24} sm={24} md={12}>
                                            <Form.Item
                                                label={<IntlMessage id="familyName" />}
                                                name="familyName"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: <IntlMessage id="form.required" />
                                                    }
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={12}>
                                            <Form.Item
                                                label={<IntlMessage id="name" />}
                                                name="name"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: <IntlMessage id="form.required" />
                                                    }
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={12}>
                                            <Form.Item
                                                label={<IntlMessage id="phone" />}
                                                name="phone"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: <IntlMessage id="form.required" />
                                                    },
                                                    {
                                                        validator(rule, value) {
                                                            if (!phoneRegEx.test(value)) {
                                                                return Promise.reject('Утасны дугаар буруу байна!');
                                                            }
                                                            return Promise.resolve();
                                                        },
                                                    }
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={12}>
                                            <Form.Item
                                                label={<IntlMessage id="email" />}
                                                name="email"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: <IntlMessage id="form.required" />
                                                    },
                                                    {
                                                        type: 'email',
                                                        message: 'И-мэйл буруу байна'
                                                    },
                                                ]}
                                            >
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                        <Col xs={24} sm={24} md={24}>
                                            <Form.Item
                                                label={<IntlMessage id="address" />}
                                                name="address"
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: <IntlMessage id="form.required" />
                                                    }
                                                ]}
                                            >
                                                <TextArea rows={4} />
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Form.Item className='text-right'>
                                        <Button type="primary" htmlType="submit" loading={updateLoading}>
                                            {<IntlMessage id="main.okText" />}
                                        </Button>
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </Col>
            </Row>
        </Card>
    )
};

export default UpdateProfile;
